// VARIABLES & MIXINS
// ------------------

// Prevent Bootstrap Upgrading errors
@import "../../../base/variables.less";

// Bootstrap mixins
@import "../../../base/mixins.less";

// Plazart Base variables
@import "variables";



//
// MOBILE
// ---------------------------------------------------------
@media (max-width: @navbarCollapseWidth) {
  .plazart-megamenu {

  // THE MEGAMENU
  //------------------------------------------------------

  // Global Menu Inner padding
  // -------------------------
    .mega-inner {
      padding: (@PlazartglobalPadding / 2) @PlazartglobalPadding;
    }

  // Inner Padding for 1 column
    .span12 .mega-inner {
    }


  // Menu Grids
  // ----------
    .row-fluid,
    .mega-dropdown-menu,
    .row-fluid [class*="span"] {
      width: 100% !important;
      min-width: 100% !important;
      left: 0 !important;
      margin-left: 0 !important;
      transform: none !important;
      -webkit-transform: none !important;
      -moz-transform: none !important;
      -ms-transform: none !important;
      -o-transform: none !important;
    }

    .row-fluid + .row-fluid  {
      padding-top: @PlazartglobalPadding / 2;
      border-top: 1px solid @hrBorder;
    }

    .row-fluid [class*="span"] {
    }

  // Hidden when collapse
    .hidden-collapse,
    .always-show  .caret,
    .sub-hidden-collapse > .nav-child,
    .sub-hidden-collapse .caret,
    .sub-hidden-collapse > a:after,
    .always-show .dropdown-submenu > a:after
    {
      display: none !important;
    }

    // THE MEGAMENU
    //------------------------------------------------------

    // Global Menu Inner padding
    // -------------------------
    .mega-inner {
      padding: (@PlazartglobalPadding / 2) @PlazartglobalPadding;
    }

    // Inner Padding for 1 column
    .span12 .mega-inner {
    }


    // Menu Grids
    // ----------
    .row-fluid,
    .mega-dropdown-menu,
    .row-fluid [class*="span"] {
      width: 100% !important;
      min-width: 100% !important;
      left: 0 !important;
      margin-left: 0 !important;
      transform: none !important;
      -webkit-transform: none !important;
      -moz-transform: none !important;
      -ms-transform: none !important;
      -o-transform: none !important;
    }

    .row-fluid + .row-fluid  {
      padding-top: @PlazartglobalPadding / 2;
      border-top: 1px solid @hrBorder;
    }

    .row-fluid [class*="span"] {
    }

    // Hidden when collapse
    .hidden-collapse,
    .always-show  .caret,
    .sub-hidden-collapse > .nav-child,
    .sub-hidden-collapse .caret,
    .sub-hidden-collapse > a:after,
    .always-show .dropdown-submenu > a:after
     {
      display: none !important;
    }
  }
}